  getStations: function ({
    state,
    commit,
    dispatch
  }) {
    try{
    commit('set_loading', true);
    Vue.http.get("https://intranet.valuout.com/CloverServices/api/kiosko/GetStation")
      .then(response => {
        if (response.body.Stations != "") {
          commit('set_stations', response.body.Stations);
        }
      }, response => {
        dispatch('set_showMessage', response.statusText + "," + "Problem comunication with the server, " + " GetStation");
        commit('set_loading', false);
      });
    commit('set_loading', false);
  } catch (e) {
    dispatch('set_showMessage', 'Problem with Application');
    commit('set_loading', false);
  }
  },
  getModels: function ({
    commit,
    state,
    dispatch
  }, datos) {
    try{
    commit('set_loading', true);
    Vue.http.get(
        "https://intranet.valuout.com/CloverServices/api/kiosko/GetCellFamilyByStationd_Lineid", {
          params: {
            station_id: datos.station_id
          }
        }
      )
      .then(response => {
        if (response.body.Cellfamilies != "") {
          var data = {
            models: response.body.Cellfamilies,
            station_id: datos.station_id
          }
          commit('set_models', data);
          commit('set_ModelStationSelect', datos);
          if (state.panel.downTime.datainfo.model_id > 0) {
            data.model_id = state.panel.downTime.datainfo.model_id
            data.model_description = state.panel.downTime.datainfo.model_description;
            data.station_name = state.panel.downTime.datainfo.station_name;
            data.station_id = state.panel.downTime.datainfo.station_id;
            dispatch('getWorkInstruction', data);
          }

        }
        commit('set_loading', false);
      }, response => {
        //error message
        dispatch('set_showMessage', response.statusText + "," + "Problem comunication with the server, " + " GetCellFamilyByStationd_Lineid");
        commit('set_loading', false);
      });
    } catch (e) {
      dispatch('set_showMessage', 'Problem with Application');
      commit('set_loading', false);
    }

  },

  getWorkInstruction: function ({
    state,
    commit,
    dispatch
  }, datos) {

    try {

      commit('set_loading', true);
      Vue.http.get(
          "https://intranet.valuout.com/CloverServices/api/kiosko/GetWksFirstInstruccionAnonymous", {
            params: {
              station_id: state.panel.downTime.datainfo.station_id,
              cellfamily_id: datos.model_id
            }
          })
        .then(response => {
          if (response.body.GetWorkInstrucction.length > 0 || response.body.Images.length == 0) {
            var npages = 0,
              imags = [];
            //  console.log("cantidad de imagenes: " + response.body.Images.length);
            imags = response.body.Images;
            commit('set_loading', false);

            commit('reset_Imageswk'); //reset array
            commit('set_Imageswk', imags); // asig first img
            //   commit('set_swPanel', 'panelWorkStruction');// show first instruction

            commit('set_ModelStationSelect', datos);


            // get all but not the first instruction
            Vue.http.get(
                "https://intranet.valuout.com/CloverServices/api/kiosko/GetWksAllBNotFirstInstruccionAnonymous", {
                  params: {
                    station_id: state.panel.downTime.datainfo.station_id,
                    cellfamily_id: datos.model_id
                  }

                }
              )
              .then(response => {
                commit('set_Imageswk', response.body.Images); // push the rest of the instructions
                dispatch('set_swPanelonly', 'FSpanelWorkStruction'); //opem fullscreen
              }, response => {
                //shoe error message  
                dispatch('set_showMessage', response.statusText + "," + "Problem comunication with the server, " + " GetWksAllBNotFirstInstruccionAnonymous");
                commit('set_loading', false);
              });


          } else {
            //shoe error message  
            dispatch('set_showMessage', 'No contain Instructions');
            commit('set_loading', false);


          }
        }, response => {
          dispatch('set_showMessage', response.statusText + ", " + "Problem comunication with the server, " + "GetWksFirstInstruccionAnonymous", );
          commit('set_loading', false);
        });

    } catch (e) {
      dispatch('set_showMessage', 'Problem with Application');
      commit('set_loading', false);
    }

  },
