  get_token: function ({
    commit,
    dispatch
  }, userinfo) {
    try{
    commit('set_loading', true);
    Vue.http.post(
        "https://intranet.valuout.com/CloverServices/auth/token", {
          username: userinfo.username,
          passwordhind: userinfo.password,
          password: "cl0v3r",
          grant_type: "password",
          client: "TRESS",
          work_station: userinfo.work_station
        }, {
          emulateJSON: true
        }
      )
      .then(response => {
        var token = "bearer" + " " + response.body.access_token;
        commit('set_token', token);

        if (userinfo.from == 'url') {
          commit('set_nav', 'navuserinstation');
          commit('set_swPanel', 'navuserinstation');
          commit('set_workstation', userinfo.work_station)
          dispatch('GetCellFamilyByWks', userinfo.username);
        } else {
          commit('set_nav', 'navnav');
          commit('set_restarttime');
          dispatch('get_stationsdwnt');
          commit('set_loading', false);
        }

      }, response => {
        dispatch('set_showMessage', response.body.error, );
        commit('set_loading', false);
      });
    } catch (e) {
      dispatch('set_showMessage', 'Problem with Application');
      commit('set_loading', false);
    }
  },


