 
 <template>
 <div class="row">
      <div class="col-7 col-sm-5 col-md-5 col-lg-2 col-xl-2 mb-2">
        <p class="f1 text-red">DOWNTIME</p>
      </div>
      <div class="col-2 col-sm-5 col-md-5 col-lg-9 col-xl-9 "></div>
      <div class="col-3 col-sm-2 col-md-2 col-lg-1 col-xl-1">
        <b-button class="btn btn-danger w-100" variant="info" v-on:click="onClose()"><b>X</b></b-button>
      </div>
          
    </div>

    </template>

    
<script>
  export default {
    name: 'headerdowntime',
    data() {
      return {}
    },
    methods:{        
      onClose: function () {
        this.$store.dispatch('close', 'panelWorkStruction');
      }
    }    
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
</style>


