<template>
  <b-container class="text-white font-weight-bold ">
       <headerdowntime class="order-0"></headerdowntime> 
 

    <div class="row">
      <div class="col-12 col-sm-12 col-md-6 col-lg-5 col-xl-5  order-2 order-md-1 order-lg-1 ">
        <!-- downtime info -->     
        <downtimeinfo class="d-none d-sm-none d-md-block d-lg-block"></downtimeinfo>      
        <br>
        <!-- TIME Info -->
        <downtimetimeinfo></downtimetimeinfo>
      </div>    

      <div class="col-12 col-sm-12 col-md-6 col-lg-7 col-xl-7 order-1 order-md-2 order-lg-1">       
        <div class="row">

          <div class="col-8 col-sm-9 col-md-8 col-lg-9 col-xl-9">
            <b-form-group id="exampleInputGroup1" v-on:keyup.enter="saveRequired" label="REQUIRED BY:" label-for="requiredby"
              description="">
              <b-form-input id="requiredby" type="password" value="" required placeholder="Employee Number">
              </b-form-input>
            </b-form-group>
          </div>

          <div class="col-4 col-sm-3 col-md-4 col-lg-3 col-xl-3">
            <button type="button" class="btn btn-downtt f4" v-on:click="saveRequired"><b>SAVE</b>
            </button>
          </div>
        </div>
        <div class="row">
          <h6 class=" text-right ml-5">{{get_datainfo.supervisor_by_firma}}</h6>
        </div>
        <br>

        <div class="row">

          <div class="col-8 col-sm-9 col-md-8 col-lg-9 col-xl-9">
            <b-form-group id="exampleInputGroup1" v-on:keyup.enter="saveAttenden()" label="ATTENDENT BY:" label-for="attendentby"
              description="">
              <b-form-input id="attendentby" type="password" value="" required placeholder="Employee Number">
              </b-form-input>
            </b-form-group>
          </div>

          <div class="col-4 col-sm-3 col-md-4 col-lg-3 col-xl-3">
            <button type="button" class="btn btn-downtt f4" v-on:click="saveAttenden()"><b>SAVE</b>
            </button>
          </div>
        </div>
        <div class="row">
          <h6 class=" text-right ml-5">{{get_datainfo.atendent_by}}</h6>
        </div>

        <div class="row mt-4">
          <b-col cols="6">           
            <b-btn class=" w-100  font-weight-bold f4" variant="danger" v-on:click="completedowntime()">COMPLETE DOWNTIME</b-btn>
            
          </b-col>
          <b-col cols="6">         
               <b-btn class="w-100 mb-5 font-weight-bold f4" variant="danger" v-on:click="canceldowntime()">CANCEL DOWNTIME</b-btn>            
          </b-col>
        </div>




      </div>
    </div>


  </b-container>
</template>


<script>
  import downtimeinfo from './downtimeinfo';
  import downtimetimeinfo from './downtimetimeinfo';
  import headerdowntime from './headerdowntime';

  export default {
    name: 'step4_signatures',
    components: {
      downtimeinfo,
      downtimetimeinfo,
      headerdowntime
    },
    data() {
      return {
        code_requiredby: '',
        code_attendentby: ''
      }
    },
    computed: {
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    },
    methods: {
      onClose: function () {
        this.$store.dispatch('close', 'panelWorkStruction');
      },
      saveRequired: function () {
        this.code_requiredby = document.getElementById('requiredby').value;

        var data = {
          signature: 'requiredby',
          user_code: this.code_requiredby,
          by:'requered'
        };
        this.$store.dispatch('onValidUser', data);
        document.getElementById('requiredby').value = "";

      },
      saveAttenden: function () {
        this.code_attendentby = document.getElementById('attendentby').value;
        var data = {
          signature: 'attendentby',
          user_code: this.code_attendentby,
          by:'attendent'
        };

        this.$store.dispatch('onValidUser', data);
        document.getElementById('attendentby').value = "";
      },
      completedowntime: function () {
        if (this.get_datainfo.supervisor_by_firma == "" || this.get_datainfo.atendent_by == "") { 
          this.$store.dispatch('set_showMessage', 'Fill the Fields');
        } else {

          var data = {
            code_requiredby: this.code_requiredby,
            code_attendentby: this.code_attendentby
          };

          this.$store.dispatch('onCloseDownTime', data);
          document.getElementById('attendentby').value = "";
        }
      },
      canceldowntime: function () {
        if (this.get_datainfo.supervisor_by_firma == "" || this.get_datainfo.atendent_by == "") {          
          this.$store.dispatch('set_showMessage', 'Fill the Fields');
        } else {
          var data = {
            code_requiredby: this.code_requiredby,
            code_attendentby: this.code_attendentby
          };
          this.$store.dispatch('onCancelDownTime', data);
        }
      }
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
  @import "../../assets/css/global.css";
    @import "../../assets/css/media_query.css";
</style>


