export default {
  set_token: (state, value) => {
    state.token = value;
  },
  set_stations: (state, stations) => {
    state.panel.workInstructions.SourceStations = stations;
  },
  set_models: (state, data) => {
    state.panel.workInstructions.SourceModels = data.models;
    state.panel.downTime.datainfo.station_id = data.station_id;
  },
  set_Imageswk: (state, images) => {
    var sources= state.panel.workInstructions.SourceImages;
    state.panel.workInstructions.SourceImages = [];
    Array.prototype.push.apply(sources, images);
    state.panel.workInstructions.SourceImages =sources;
  },
 reset_Imageswk: (state) => {
    state.panel.workInstructions.SourceImages = [];
  },
  set_listDowntimes: (state, data) => {
    state.panel.downTime.listDowntimes = data;
  },
  set_lines: (state, data) => {
    state.panel.downTime.lines = data;
  },
  set_dwnstations: (state, data) => {
    state.panel.downTime.stations = data;
  },
  set_causas: (state, data) => {
    state.panel.downTime.causas = data;
  },

  set_loading: (state, value) => {
    state.loadconfig.isloading = value;
  },
  set_colorLoading: (state, value) => {
    state.loadconfig.bgcolor = value;
    state.loadconfig.color = value;
  },
  set_datainfo: (state, data) => {
    if (data.station_id != "")
      state.panel.downTime.datainfo.station_id = data.station_id;
    if (data.station_name != "")
      state.panel.downTime.datainfo.station_name = data.station_name;
    if (data.cause_name != "")
      state.panel.downTime.datainfo.cause_name = data.cause_name;
    if (data.cause_id != "")
      state.panel.downTime.datainfo.cause_id = data.cause_id;
    if (data.line_id != "")
      state.panel.downTime.datainfo.line_id = data.line_id;
    if (data.line_name != "")
      state.panel.downTime.datainfo.line_name = data.line_name;         
  },
  from: (state, from) => {
    state.panel.downTime.from = from;
  },
  setTimes: (state, data) => {
    switch (data.status) {
      case 'Open':
        state.panel.downTime.datainfo.timestart = new Date(data.dt_start).toLocaleString();
        break;
      case 'Pause':
        state.panel.downTime.datainfo.timestart = new Date(data.dt_start).toLocaleString();
        state.panel.downTime.datainfo.timecheck = new Date(data.dt_stop).toLocaleString();
        break;
      case 'Cancel':
      case 'Closed':
        state.panel.downTime.datainfo.timestart = new Date(data.dt_start).toLocaleString();
        state.panel.downTime.datainfo.timecheck = new Date(data.dt_stop).toLocaleString();
        state.panel.downTime.datainfo.timeclosed = new Date(data.dt_closed).toLocaleString();
        state.panel.downTime.datainfo.timedowntime = data.timeresumestoptoclose;
        break;

    }
  },
  set_userInfo:(state,data)=>{
    state.panel.navuserinstation.computer_name = data.computer_name;
    state.panel.navuserinstation.line_name = data.line_name;
    state.panel.navuserinstation.station = data.station;
    state.panel.navuserinstation.model = data.model;
    state.panel.navuserinstation.iscertificated = data.iscertificated;
    state.panel.navuserinstation.name = data.name;
    state.panel.navuserinstation.photo = data.photo;

    state.panel.downTime.datainfo.station_id = data.station_id;
    state.panel.downTime.datainfo.model_id = data.model_id;

  },
  set_nav:(state,nav)=>{
    state.panel.swnav = nav;
  },
  
  set_workstation:(state,station)=>{
    state.panel.navuserinstation.workstation= station;
  },

  startTime: (state, openTime) => {
    var esto = this,
      hour = 0,
      min = 0,
      second = 0;

    state.timedowntime = "";

    if (openTime != "") {
      second = parseInt((openTime / 1000) % 60),
        min = parseInt((openTime / (1000 * 60)) % 60),
        hour = parseInt((openTime / (1000 * 60 * 60)) % 24);
    }

    var time = setInterval(function () {
      second++;
      if (second > 59) {
        second = 0;
        min++;
        if (min > 59) {
          hour++;
          min = 0;
        }
      }
      if(state.panel.downTime.datainfo.time  == -1){
        clearInterval(time);
      }


      state.panel.downTime.datainfo.timedowntime = (hour > 9 ? hour : '0' + hour) + ':' + (min > 9 ? min : '0' + min) + ':' + (second >
        9 ? second : '0' + second);
    }, 1000);
  },
  set_restarttime:(state) =>{
    state.panel.downTime.datainfo.time = "";
  },
  onStopTime: (state) => {    
    state.panel.downTime.datainfo.time = -1;
  },
  setstartinfo: (state, data) => {
    state.panel.downTime.datainfo.department_cause = data.department_cause;
    state.panel.downTime.datainfo.supervisor_by = data.supervisor_by;
  },
  UsersInfo: (state, data) => {
    state.panel.downTime.datainfo.atendent_by = data.atendent_by;
    state.panel.downTime.datainfo.supervisor_by = data.supervisor_by;
  },
  set_swPanel: (state, panel) => {
    state.panel.swpanel = panel;   
      state.loadconfig.bgcolor = '#064d75';
      state.loadconfig.color = '#064d75';

      state.panel.downTime.from = '';
      state.panel.downTime.listDowntimes = [];
      state.panel.downTime.lines = [];
      state.panel.downTime.stations = [];
      state.panel.downTime.causas = [];           
      state.panel.downTime.datainfo.timedowntime = '';
      state.panel.downTime.datainfo.timestart = '';
      state.panel.downTime.datainfo.timecheck = '';
      state.panel.downTime.datainfo.timeclosed = '';
      state.panel.downTime.datainfo.timeresumestarttocheck = '';
      state.panel.downTime.datainfo.timeresumestoptoclose = '';
      state.panel.downTime.datainfo.department_cause = '';
      state.panel.downTime.datainfo.atendent_by = '';
      state.panel.downTime.datainfo.supervisor_by = '';
      state.panel.downTime.datainfo.supervisor_by_firma = '';
      state.panel.downTime.datainfo.comments = '';
      state.panel.downTime.datainfo.countermeasure = '';
      state.panel.downTime.datainfo.station_id = '';
      state.panel.downTime.datainfo.station_name = '';
      state.panel.downTime.datainfo.line_id = '';
      state.panel.downTime.datainfo.line_name = '';
      state.panel.downTime.datainfo.cause_id = '';
      state.panel.downTime.datainfo.cause_name = '';
      state.panel.downTime.datainfo.deparment = '';
    

  },
  set_swPanelonly: (state, panel) => {
    state.panel.swpanel = panel;   
  },
  set_ModelStationSelect:(state, data) => {
    state.panel.downTime.datainfo.model_id = data.model_id;
    state.panel.downTime.datainfo.model_description =data.model_description;
    state.panel.downTime.datainfo.station_id = data.station_id;
    state.panel.downTime.datainfo.station_name =data.station_name;
  },
  set_swdwntPanel: (state, panel) => {
    state.panel.swdwnt_panel = panel;
  },
  set_showMessage: (state, message) => {
    state.modalmessage.message = message;
    state.modalmessage.show = true;
  },
  set_HideMessage: (state, message) => {
    state.modalmessage.message = message;
    state.modalmessage.show = false;
  },
  set_firmas: (state, data) => {
    if (data.signature == 'attendentby') {
      state.panel.downTime.datainfo.atendent_by = data.name;
    } else if (data.signature == 'requiredby') {
      state.panel.downTime.datainfo.supervisor_by_firma = data.name;
    }
  }
}

