import Vue from 'vue'
import Vuex from 'vuex'

import VueResource from 'vue-resource'
Vue.use(VueResource)

import actions from './actions';
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    token: null,       
    modalmessage:{
      show:false,
      message:''
    },
    loadconfig: {
      fullPage: true,
      isloading:false,
      color: '#064d75', //#3e0508  - rojo
      bgcolor: '#064d75', //#064d75 - verde
      height: 255,
      width: 255,
      loader: 'bars',
    },
    panel: {
      swpanel:'',
      swnav:'',
      swdwnt_panel:'',
      workInstructions: {      
        SourceStations: [],
        SourceModels: [],
        SourceImages: [],
        Station: {
          station_id: 0,
          station_name: ''
        },
        Models: {
          model_id: 0,
          model_name: ''
        }
      },
      downTime: { 
        from:'', /* fromPause, fromFinish, fromCancel,   */
        listDowntimes:[],
        lines:[],
        stations:[],
        causas:[], 
        datainfo:{      
          time:'', 
          timedowntime:'',
          timestart:'',
          timecheck:'',
          timeclosed:'',
          timeresumestarttocheck:'',          
          timeresumestoptoclose:'',
          department_cause:'',

         
          atendent_by:'',        
          supervisor_by:'',
          supervisor_by_firma:'',       
          comments:'',
          countermeasure:'',

          station_id:'',
          station_name:'',
          line_id:'',
          line_name:'',
          cause_id: '',
          cause_name:'',
          deparment:'',
          model_id:'',
          model_description:''
        }
      },
      navuserinstation:{
        computer_name:'',
        username:'',
        line_name:'',
        station:'',
        model:'',
        iscertificated:'',
        photo:'',
        workstation:''
      }
    },
    Visibles: {
      showNavbar: false,
    }
  },

  /*MUTATIONS CHANGES THE VARIABLES OF THE STATE */
  mutations: mutations,
  /* Instead of mutating the state, actions commit mutations. Events and they are Actions can contain arbitrary asynchronous operations */
  actions: actions,
  getters: getters
});


