<template>
  <div>
    <div class="row ml-0  ml-md-0 ml-lg-0">
         
      <div class="col">
        <hr class="linecolor">
        <div class="row">
          <div class="col-5 col-sm-5 col-md-5 col-lg-5 col-xl-5">
            <h5 class="font-weight-bold f4 "><i class="fas fa-play-circle text-white"></i> START:</h5>
          </div>
          <div>
            <h5 class="font-weight-bold text-red f4"> {{get_datainfo.timestart}}</h5>
          </div>
        </div>

        <div class="row">
          <div class="col-6 col-sm-5 col-md-5 col-lg-5 col-xl-5">
            <h5 class="font-weight-bold f4"><i class="fas fa-check-circle text-white"></i> CHECK: </h5>
          </div>
          <div>
            <h5 class="font-weight-bold text-red ml-51 ml-lg-0 f4"> {{get_datainfo.timecheck}}</h5>
          </div>
        </div>

        <div class="row">
          <div class="col-6 col-sm-5 col-md-5 col-lg-5 col-xl-5">
            <h5 class="font-weight-bold f4"><i class="fas fa-stop-circle text-white"></i> CLOSED:</h5>

          </div>
          <div class="col">
            <h5 class="font-weight-bold text-red ml-51 ml-lg-0 f4">{{get_datainfo.timeclosed}}</h5>
          </div>
        </div>

        <b-row>
          <div class="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12">
            <h5 class="font-weight-bold f4"><i class="fas fa-user-cog text-white"></i> ATTENDENT BY: </h5>
          </div>
          <div class="col">
            <h5 class="font-weight-bold ml-5 f4"> {{get_datainfo.atendent_by}}</h5>
          </div>
        </b-row>
        <hr class="linecolorred">
        <div class="row">
          <div class="col-12 col-sm-5 col-md-5 col-lg-5 col-xl-5">
            <h5 class="font-weight-bold f4"><i class="fas fa-clock text-white"></i> DOWNTIME:</h5>
          </div>
          <div class="col text-center">
            <h1 class="font-weight-bold text-red "> {{get_datainfo.timedowntime}}</h1>
          </div>
        </div>

        <hr class="linecolorred">

      </div>
    </div>
  </div>
</template>


<script>
  export default {
    name: 'downtimetimeinfo',
    data() {
      return {}
    },
    computed: {
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
</style>


