<template>
  <b-container fluid class="indezz">       
        <swiper :options="swiperOption">
          <swiper-slide v-for="(slide, index) in get_wkimages" :key="index">
            <img :src="slide" class="immg"  >         
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
  </b-container>
</template>

<script>
  export default {
    name: '',
    components: {},
    data() {
      return {       
        swiperOption: {
          spaceBetween: 30,
          centeredSlides: true,
          autoplay: {
            delay: 10000,
            disableOnInteraction: false
          }
        },
        
      }
    },
    computed: {
      get_wkimages() {
        return this.$store.getters.get_wkimages;
      }
    }   

  }
</script>

<style scoped>
  @import "../../assets/css/panelWorkStation.css";
   @import "../../assets/css/global.css";

</style>


