<template>
  <div class="container text-white font-weight-bold">
    <headerdowntime class="order-0"></headerdowntime>

    <div class="row">
      <div class="mt-2 col-12 col-sm-12 col-md-12 col-lg-5 col-xl-5  order-2 order-md-2 order-lg-1 ">
        <!-- downtime info -->
        <downtimeinfo class="d-none d-sm-none d-md-none d-lg-block"></downtimeinfo>
        <br>
        <!-- TIME Info -->
        <downtimetimeinfo></downtimetimeinfo>
      </div>

      <div class="col-12 col-sm-12 col-md-12 col-lg-7 col-xl-7 order-1 order-md-1 order-lg-1">
        <div class="row">       
          <div class="col-12">
            <input class="mb-4" type="checkbox" aria-label="PermanentC" id="checkPermanentCP" ref="checkPermanentCP">
            <h5 class="inline text-white font-weight-bold"><b>IS PERMANENT?</b></h5>
          </div>
        </div>
        <div class="row">
          <div class="col-12 col-sm-6 col-md-12 col-lg-6 col-xl-6">
            <p class="text-white text-left font-weight-bold f3"><b>COMMENT:</b> </p>
            <textarea class="form-control w-100 mb-4" id="inputCommentCP" ref="inputCommentCP" rows="3"></textarea>
          </div>
          <div class="col-12 col-sm-6 col-md-12 col-lg-6 col-xl-6">
            <p class="text-white text-left font-weight-bold f3"><b>COUNTERMEASURE:</b> </p>
            <textarea class="form-control w-100" id="inputCountermeasureCP" ref="inputCountermeasureCP" rows="3"></textarea>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
          <b-btn class="mt-3 w-100  font-weight-bold f3" variant="danger" v-on:click="finish()">FINISH</b-btn>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import downtimeinfo from './downtimeinfo';
  import downtimetimeinfo from './downtimetimeinfo';
  import headerdowntime from './headerdowntime';

  export default {
    name: 'step5_complete',
    components: {
      downtimeinfo,
      downtimetimeinfo,
      headerdowntime
    },
    data() {
      return {}
    },
    computed: {
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    },
    methods: {
      finish: function () {
        var comment = document.getElementById('inputCommentCP').value;
        var countermeasure = document.getElementById('inputCountermeasureCP').value;
        var parmanet = document.getElementById('checkPermanentCP').checked;

         if (comment == "" || countermeasure == "") {
        
          this.$store.dispatch('set_showMessage', 'Fill the Comments and  Countermeasure');
        } else {

        var data = {
          comment: comment,
          countermeasure: countermeasure,
          parmanet: parmanet
        };

        this.$store.dispatch('oncommentsCountermeasure', data);
        document.getElementById('inputCommentCP').value = "";
        document.getElementById('inputCountermeasureCP').value = "";
        document.getElementById('checkPermanentCP').checked = false;
}
      },
      onClose: function () {
        this.$store.dispatch('set_swPanel', 'panelWorkStruction');
      }
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
  @import "../../assets/css/global.css";
  @import "../../assets/css/media_query.css";
</style>


