<template>
  <b-container class="text-white font-weight-bold">
    <headerdowntime></headerdowntime>

    <div class="row">
      <!--SELECT THE LINE -->
      <div class="col-12 col-sm-12 col-md-12 col-lg-4 col-xl-4 pdtp text-center">
        <b-row>
          <b-col v-if="!showSelectedLine">
            <hr class="linecolor">
            <h5 CL class="f3 font-weight-bold">SELECT THE LINE</h5>
            <hr class="linecolor">
          </b-col>

          <b-col v-if="showSelectedLine">
            <hr class="linecolor">
            <h5 class="f3 font-weight-bold">SELECTED LINE</h5>
            <hr class="linecolor">
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <div v-if="!showSelectedLine" class="divLines">
              <b-list-group v-for="linea in get_lines" :key="linea.linea_id">
                <b-button class="favorite mt-3 btnwithdowntimefilter btn-danger" variant="danger" v-text="linea.line_name"
                  v-on:click="onClickLinea(linea.line_id,linea.line_name)">
                </b-button>
              </b-list-group>


            </div>
            <h2 v-if="showSelectedLine" class="text-white"><u>{{get_datainfo.line_name}}</u> </h2>
            <a v-if="showSelectedLine" class="linkclick" v-on:click="changeLine()">Change the Line</a>

          </b-col>
        </b-row>


      </div>
      <br>
      <!-- SELECT THE STATION -->
      <div class=" col-12 col-sm-12 col-md-12 col-lg-4 col-xl-4 pdtp text-center" >
        <b-row>
          <b-col v-if="!showSelectedstation">
            <hr class="linecolor">
            <h5 class="f3 font-weight-bold">SELECT THE STATION</h5>
            <hr class="linecolor">
          </b-col>

          <b-col v-if="showSelectedstation">
            <hr class="linecolor">
            <h5 class="f3 font-weight-bold">SELECTED STATION</h5>
            <hr class="linecolor">
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <div v-if="!showSelectedstation" class="divStations">
              <b-list-group v-for="station in get_dnstations" :key="station.station_id">
                <b-button class="favorite mt-3 btnwithdowntimefilter btn-danger" variant="danger" v-text="station.station_name"
                  v-on:click="onClickStation(station.station_id,station.station_name)">
                </b-button>
              </b-list-group>
              <div v-if="get_dnstations < 0">
                <h2 v-if="showSelectedstation" class="text-white"> Not Existin causes </h2>
              </div>


            </div>
            <h2 v-if="showSelectedstation" class="text-white"><u>{{get_datainfo.station_name}} </u></h2>
            <a v-if="showSelectedstation" class="linkclick" v-on:click="changeStation()">Change the Station</a>

          </b-col>
        </b-row>


      </div>


      <!-- SELECT THE CAUSE-->
      <div class="col-12 col-sm-12 col-md-12 col-lg-4 col-xl-4 pdtp text-center" >
        <b-row>
          <b-col v-if="!showSelectedCause">
            <hr class="linecolor">
            <h5 class="f3 font-weight-bold">SELECT THE CAUSE</h5>
            <hr class="linecolor">
          </b-col>

          <b-col v-if="showSelectedCause">
            <hr class="linecolor">
            <h5 class="f3 font-weight-bold">SELECTED CAUSE</h5>
            <hr class="linecolor">
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <div v-if="!showSelectedCause" class="">
              <b-list-group v-for="cause in get_causas" :key="cause.linea_id">
                <b-button class="favorite mt-3 btn-danger btnwithdowntimefilter" variant="danger" v-text="cause.cause"
                  v-on:click="onClickCausa(cause.dt_cause_id,cause.cause)">
                </b-button>
              </b-list-group>
            </div>
            <h2 v-if="showSelectedCause" class="text-white "><u>{{get_datainfo.cause_name}}</u></h2>
            <a v-if="showSelectedCause" class="linkclick" v-on:click="changeCause()">Change the Cause</a>

          </b-col>
        </b-row>
      </div>
    </div>

    <div class="row">
      <div class="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12" v-if="(showSelectedLine &&  showSelectedCause && showSelectedstation)">
        <b-btn class="mt-3 w-100 mb-5 btnwithdowntimefilter f3" variant="danger" v-on:click="OnContinue()">CONTINUE</b-btn>

      </div>
    </div>


  </b-container>
</template>


<script>
  import headerdowntime from './headerdowntime';


  export default {
    name: 'step2_options',
    components: {
      headerdowntime
    },
    data() {
      return {
        Message: '',
        showSelectedLine: false,
        showSelectedstation: false,
        showSelectedCause: false,
        dataInfo: {
          station_id: '',
          station_name: '',
          line_id: '',
          line_name: '',
          cause_id: '',
          cause_name: ''
        }
      }
    },
    computed: {
      get_dnstations() {
        return this.$store.getters.get_dnstations;
      },
      get_lines() {
        return this.$store.getters.get_lines;
      },
      get_causas() {
        return this.$store.getters.get_causas;
      },
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    },
    methods: {
      OnContinue: function () {
        this.$store.dispatch('GetDowtimeWhenRefresh');

      },
      onClickLinea: function (line_id, line_name) {
        var esto = this;
        esto.showSelectedLine = true;

        esto.dataInfo.line_id = line_id;
        esto.dataInfo.line_name = line_name;
        esto.$store.dispatch('set_datainfo', esto.dataInfo);

      },
      changeLine: function () {
        var esto = this;
        esto.showSelectedLine = false;
      },
      onClickStation: function (station_id, station_name) {
        var esto = this;
        esto.showSelectedstation = true;

        esto.dataInfo.station_id = station_id;
        esto.dataInfo.station_name = station_name;
        esto.$store.dispatch('set_datainfo', esto.dataInfo);

        esto.$store.dispatch('get_causas', station_id);
      },
      changeStation: function () {
        var esto = this;
        esto.showSelectedstation = false;
        esto.showSelectedCause = false;

      },
      onClickCausa: function (cause_id, cause_name) {
        var esto = this;
        esto.showSelectedCause = true;

        esto.dataInfo.cause_id = cause_id;
        esto.dataInfo.cause_name = cause_name;
        esto.$store.dispatch('set_datainfo', esto.dataInfo);


      },
      changeCause: function () {
        var esto = this;
        esto.showSelectedCause = false;
      },
      onClose: function () {
        this.$store.dispatch('close', 'panelWorkStruction');
      }
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
  @import "../../assets/css/global.css";
  @import "../../assets/css/media_query.css";
</style>


