<template>
  <b-container fluid class="indezz">
      <step1login v-if="gswdwnt_panel === 'step1_login'"></step1login>

    <step2options v-if="gswdwnt_panel === 'step2_options'"></step2options>

    <step3timeCheck v-if="gswdwnt_panel === 'step3_timeCheck'"></step3timeCheck>

    <step4signatures v-if="gswdwnt_panel === 'step4_signatures'"></step4signatures>

    <step5complete v-if="gswdwnt_panel === 'step5_complete'"></step5complete> 

    <step5cancel v-if="gswdwnt_panel === 'step5_cancel'"></step5cancel> 

    <downtimeson v-if="gswdwnt_panel === 'downtimeson'"></downtimeson>
    

    
    
    <!-- v-if="gswdwnt_panel === 'step2_options'" -->
  </b-container >

</template>

<script>
 
  import step1login from './step1_login';
  import step2options from './step2_options';
  import step3timeCheck from './step3_timeCheck';
  import step4signatures from './step4_signatures';
  import step5complete from './step5_complete';
  import step5cancel from './step5_cancel';  
   import downtimeson from './downtimeson';
   
  
  

  export default {
    name: 'maindowntime',
    components: {
      downtimeson,
      step1login,
      step2options,
      step3timeCheck,
      step4signatures,
      step5complete,
      step5cancel

    },
    data() {
      return {}
    },
    computed: {
      gswdwnt_panel() {
        return this.$store.getters.gswdwnt_panel;
      }     
    },
    methods: {
     
  }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
  .indezz{
  z-index: 1001;
}
</style>


