<template>
  <div class="ListDowntimesActives white-text">
    <div class="container">
      <div class="row">
        <div class="col-8 col-sm-8 col-md-8 col-lg-8 col-xl-8">
          <p class="f1 text-red"> DOWNTIME</p>
        </div>

        <div class="col">
          <b-button class="btn btn-danger w-100" variant="info" v-on:click="OnLoginDowntime()"><b>LOG IN</b></b-button>
        </div>

        <div class="col-1 col-sm-1 col-md-1 col-lg-1 col-xl-1">
          <b-button class="btn btn-danger w-100" variant="info" v-on:click="onClose()"><b>X</b></b-button>
        </div>
      </div>
      <div class="row">
        <div class="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 text-center mt-3" >
          <p class="f1 text-red"> LIST OF DOWNTIMES ACTIVES</p>
        </div>
      </div>

      <div class="row">
        <div class="col-12 col2-sm-12 col-md-12 col-lg-12 col-xl-12">
          <div class="row " v-for="i in  Math.ceil(get_listDowntimes.length / 3) " :key="i.dt_id">
            <div class="col-3 col-sm-3 col-md-3 col-lg-3 col-xl-3 ml-5 mb-5 mw-px" v-for="d in get_listDowntimes.slice((i - 1) * 3, i * 3)" :key="d.dt_id">
              <div class="CardVideoLayout">
                <b-row class="CardBody">
                  <b-col>
                    <h6><b>Line:</b> {{d.line_name}} </h6>
                    <h6><b>Station:</b> {{d.station_name}} </h6>
                    <h6><b>Cause:</b> {{d.cause}} </h6>
                  </b-col>
                </b-row>
                <b-row>
                  <b-col>
                    <b-button class="btn  btn-primary btn-block btnsn btnnClose" variant="danger" v-on:click="onDowntime(d)"><b>CONTINUE</b></b-button>
                  </b-col>
                </b-row>

              </div>
            </div>
          </div>

        </div>
      </div>

    </div>

  </div>
</template>

<script>
  export default {
    name: 'downtimeson',
    data() {
      return {}
    },
    computed: {
      get_listDowntimes() {
        return this.$store.getters.get_listDowntimes;
      }
    },
    methods: {
      onClose: function () {
        this.$store.dispatch('set_swPanel', 'panelWorkStruction');
      },
      onDowntime: function (data) {
       
        var dataInfo = {
          station_id: data.station_id,
          station_name: data.station_name,
          cause_name: data.cause,
          cause_id: data.dt_cause_id,
          line_id: data.line_id,
          line_name: data.line_name,   
          status:data.status,
          dt_start:data.dt_start,
          dt_stop:data.dt_stop,
          dt_closed:data.dt_closed,
          department_cause:data.department_cause,
          supervisor_by:data.supervisor_by

        };
       
        this.$store.dispatch('ondowntime', dataInfo);
      },
      OnLoginDowntime: function () {
        this.$store.dispatch('set_swdwntPnl', 'downtimeson');
      }
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
</style>


