<template>
  <div class="container mt-2 text-white font-weight-bold">
    <headerdowntime></headerdowntime>    
  <form>
    <div class="row">
      <div class="col-12 col-sm-6 col-md-5 col-lg-5 col-xl-5">
        <b-form-group id="exampleInputGroup1" class="f3" label="User Name:" label-for="username" description="">
          <b-form-input id="username" type="text"  placeholder="User Name" @keyup.enter="getTokenk">
          </b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-sm-6 col-md-5 col-lg-5 col-xl-5">
        <b-form-group id="exampleInputGroup2" class="f3" label="Password:" label-for="passw" description="">
          <b-form-input id="password" type="password" placeholder="Password" @keyup.enter="getTokenk">
          </b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-sm-12 col-md-2 col-lg-2 col-xl-2">      
         <b-btn class="mt-4 w-100 mb-2 font-weight-bold f3" variant="danger" v-on:click="getToken()">LOG IN</b-btn>
      </div>
    </div>
    </form>   
  </div>
</template>


<script>

  import headerdowntime from './headerdowntime';

  export default {
    name: 'step1_login',
    components:{
      headerdowntime
    },
    data() {
      return {
        Message: ''
      }
    },
    computed: {},
    methods: {
      getToken: function () {
        var esto = this;
        var username = document.getElementById('username').value,
          password = document.getElementById('password').value;

        if (username == "" || password == "") {
        
          this.$store.dispatch('set_showMessage', 'Fill the Fields');         
        } else {
          var userinfo = {
            username: username,
            password: password
          };
          this.$store.dispatch('get_token', userinfo);
        }
      },
      getTokenk: function (event) {
        if (event.code == "NumpadEnter") {
          var esto = this;
          var username = document.getElementById('username').value,
            password = document.getElementById('password').value;

          if (username == "" || password == "") {
            esto.Message = 'Llenar los campos';
            esto.$refs.modalMensaje.show();
          } else {
            var userinfo = {
              username: username,
              password: password,
              work_station:'kiosk',
              from:''
            };
            this.$store.dispatch('get_token', userinfo);
          }
        }
      },
      closeMessage: function () {
        this.$refs.modalMensaje.hide();
      }     
    },
    mounted() {
      var a = this;
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
</style>


