<template>
  <b-container class="text-white">        
        <div class="row">
          <div class="col">         

            <div class="row">
                 <hr class="linecolor">
              <div class="col-5 col-sm-5 col-md-6 col-lg-5 col-xl-5">
                <h5 class="font-weight-bold f3"><i class="fas fa-project-diagram"></i>  LINE:</h5>                  
              </div>
              <div class="col">
                <h5 class="font-weight-bold f3"> {{get_datainfo.line_name}}</h5>
              </div>
            </div>

            <div class="row">
              <div class="col-5 col-sm-5 col-md-6 col-lg-5 col-xl-5">
                <h5 class="font-weight-bold f3"><i class="fas fa-desktop"></i> STATION:</h5>                  
              </div>
              <div class="col">
                <h5 class="font-weight-bold f3">{{get_datainfo.station_name}}</h5>
              </div>
            </div>

            <div class="row">
              <div class="col-5 col-sm-5 col-md-6 col-lg-5 col-xl-5">               
                <h5 class="font-weight-bold f3"><i class="fas fa-times-circle"></i>  CAUSE:</h5>                  
              </div>
              <div class="col">
                <h5 class="font-weight-bold f3"> {{get_datainfo.cause_name}}</h5>
              </div>
            </div>

            <div class="row">
              <div class="col-5 col-sm-5 col-md-6 col-lg-6 col-xl-5">
                <h5 class="font-weight-bold f3"><i class="fas fa-chalkboard-teacher"></i>  DEPARMENT: </h5>                 
              </div>
              <div class="col">
                <h5  class="font-weight-bold f3"> {{get_datainfo.department_cause}}</h5>
              </div>
            </div>

              <div class="row">
              <div class="col-5 col-sm-5 col-md-6 col-lg-5 col-xl-5">
                <h5 class="font-weight-bold f3"><i class="fas fa-user"></i>  REQUIRED BY: </h5>                 
              </div>
              <div class="col">
                <h5  class="font-weight-bold f3"> {{get_datainfo.supervisor_by}} </h5>
              </div>
            </div>
          </div>
          
        </div>       
  </b-container>
</template>


<script>
  export default {
    name: 'downtimeinfo',
    data() {
      return {}
    },
    computed: {
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    }   
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
</style>


