<template>
  <div>
    <navbar></navbar>
    <div class="container-fluid">

      <div class="row">
        <div class="col-6 col-sm-5 col-md-4 col-lg-3 col-xl-2" v-if="gsw_nav !== 'navuserinstation'">
          <navside ></navside>         
        </div>
        <!-- e-station -->
         <div class="col-6 col-sm-5 col-md-4 col-lg-3 col-xl-2 sidebar" v-if="gsw_nav === 'navuserinstation'">
           <navuserinstation ></navuserinstation>
         </div>
         
        <div class="col-6 col-sm-7 col-md-8 col-lg-9 col-xl-10 mt-5 indezz">
          <!-- PANELS INSIDDE-->
          <panelWorkStruction class="" v-if="gsw_panel === 'panelWorkStruction'"></panelWorkStruction>
        </div>

        
      </div>

      <!-- FULL PANELS -->
      <div class="fullscreen" v-if="gsw_panel === 'FSpanelWorkStruction'">
        <div class="container">
          <div class="row">
            <div class="col-10" cols="10">
            </div>
            <div class="col">
              <button type="button" class="btn btn-primary" v-on:click="onFullScreen()"><b>FULL SCREEN</b></button>
            </div>
          </div>
          <div class="row">
            <panelWorkStruction></panelWorkStruction>
          </div>
        </div>
      </div>

      <div class="fullscreen " v-if="gsw_panel === 'panelDowntime'">
        <div class="mt-5">
          <maindowntime></maindowntime>
        </div>

      </div>

      <b-modal  variant="info" ref="modalMensaje" class="modalError" title="Error" hide-footer body-bg-variant="light"
      no-close-on-esc no-close-on-backdrop hide-header-close>
      <div class="d-block text-center ">
        <p class="colMessage"> <i class=" fas fa-exclamation-circle"></i>
       {{ get_showMessage.message }}</p>
      </div>
      <b-btn class="mt-3" variant="danger" block @click="closeMessage()"><b>OK</b></b-btn>
    </b-modal>

      <Loading :active.sync="get_loaderinfo.isloading" :height="get_loaderinfo.height" :width="get_loaderinfo.width"
        :color="get_loaderinfo.color" :loader="get_loaderinfo.loader" :background-color="get_loaderinfo.bgcolor" class="loadingz"
        :is-full-page="get_loaderinfo.fullPage"></Loading>

    </div>
  </div>



</template>

<script>
  /* components */
  // import NavBar from './NavBar'
  import navbar from './Nav/navbar';
  import navside from './Nav/navside';
  import navuserinstation from './Nav/navuserinstation';


  import maindowntime from './downtime/maindowntime';

  import Loading from 'vue-loading-overlay';
  import 'vue-loading-overlay/dist/vue-loading.css';

  /* panels */
  import panelWorkStruction from './panels/panelWorkStruction';

  export default {
    name: 'mainestationkiosk',
    components: {
      navbar,
      navside,
      navuserinstation,

      panelWorkStruction,
      maindowntime,

      Loading
    },
    data() {
      return {}
    },
    computed: {
      gsw_panel() {
        return this.$store.getters.gsw_panel;
      },
      gsw_nav(){
        return this.$store.getters.gsw_nav;
      },
      get_loaderinfo() {
        return this.$store.getters.get_loaderinfo;
      },
      get_showMessage(){
        if(this.$store.getters.get_showMessage.show){
              this.$refs.modalMensaje.show();   
        }else if(!this.$store.getters.get_showMessage.show){
          if(this.$refs.modalMensaje != undefined)
            this.$refs.modalMensaje.hide()
        }        
        return this.$store.getters.get_showMessage;
      }
    },
    created() {
      this.$store.dispatch('getStations');
    },
    methods: {
      onFullScreen: function (value) {
        this.$store.dispatch('set_swPanelonly', 'panelWorkStruction');

      },
       closeMessage: function () {   
        
         this.$store.dispatch('set_HideMessage', '');

       }
    },
    mounted: function () {
      var userid = "userid",
        pass = "",
        wks = "wks";
      var sPageURL = window.location.search.substring(1),
        sURLVariables = sPageURL.split('&');

      if (sPageURL != "") {

        for (var i = 0; i < sURLVariables.length; i++) {
          var sParameterName = sURLVariables[i].split('=');
          if (userid == sParameterName[0]) {
            userid = sParameterName[1];
          }
          if (wks == sParameterName[0]) {
            wks = sParameterName[1]
          }
        }

        var userinfo = {
          username: userid == "userid" ? 28759 : userid,
          password: pass,
          work_station: wks == "wks" ? "AND-02" : wks,
          from: 'url'
        };
        this.$store.dispatch('get_token', userinfo);
      } else {
        this.$store.dispatch('set_swPanel', 'navside');
      }
    }
  }
</script>

<style scoped>
.indezz{
  z-index: 998;
}
</style>


