<template>
  <div class="mt-69">
    <div class="main">
      <button class="navbar-toggler" type="button">
      </button>
    </div>
    <nav class="navbar navbar-expand-sm fixed-top mt-5">

      <button class="navbar-toggler blackbg text-white" type="button" data-toggle="collapse" data-target="#navbarsExample07"
        aria-controls="navbarsExample07" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse  " id="navbarsExample07">

        <div class=" navigation_cg mt-2" id="menuButton" for="sideMenuHandler">
       
          <ul class="nav_cat_cg">
            <li><a href="#"><b>STATIONS</b></a></li>
            <li class="height50">
              <ul class="submenu_cg " v-for="station in stations" :key="station.station_id">
                <li cla>
                  <a href="#" v-on:click="onStation(station)">{{ station.station_name }}</a>
                </li>
              </ul>
            </li>
            <li class="mt-1"><a href="#"><b>MODELS</b></a></li>
            <li class="height250 ">
              <ul class="submenu_cg" v-for="model in models" :key="model.cell_family_id">
                <li>
                  <a href="#" v-on:click="onModel(model)">{{ model.cell_family_description }}</a>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </nav>


  </div>

</template>

<script>
  import actions from './scripts.js';

  export default {
    name: 'navside',
    data() {
      return {
        station_id: 0,
        station_name: '',
        model_id: 0,
        model_description: ''
      }
    },
    computed: {
      stations() {
        return this.$store.getters.get_stations;
      },
      models() {
        return this.$store.getters.get_models;
      },
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    },
    methods: {
      onStation: function (station) {
        var esto = this;
        var datastation = {
          station_id: station.station_id,
          station_name: station.station_name,
          model_id: esto.model_id,
          model_description: esto.model_description,
        };
        this.station_id = station.station_id;
        this.station_name = station.station_name;

        this.$store.dispatch('getModels', datastation);
      },
      onModel: function (model) {
        var esto = this;
        var dataModel = {
          model_id: model.cell_family_id,
          model_description: model.cell_family_description,
          station_id: esto.station_id,
          station_name: esto.station_name
        };
        esto.model_id = model.cell_family_id;
        esto.model_description = model.cell_family_description;

        this.$store.dispatch('getWorkInstruction', dataModel);
      }
    },
    mounted: function () {

      $('#sidebar-btn').click(function () {
        $('#sidebar').toggleClass('visible');
      });
      // Operator
      $('#operator').click(function () {
        $('#operator_show').fadeIn('slow');
      });
      $('.close').click(function () {
        $('#operator_show').fadeOut('slow');
      });

    }
  }
</script>

<style>
  @import "../../assets/css/nav_side.css";
</style>


