export default {
  get_token(state){
    state.token;
  },
  get_stations(state) {
    return state.panel.workInstructions.SourceStations;
  },
  get_models(state) {
    return state.panel.workInstructions.SourceModels;
  },
  get_wkimages(state){
    return state.panel.workInstructions.SourceImages;
  },
  gsw_naVbar(state){
    return state.naVbar.sw_naVbar;
  }, 
  get_listDowntimes(state){
    return state.panel.downTime.listDowntimes;
  },
  get_lines(state){
    return state.panel.downTime.lines;
  },
  get_dnstations(state){
    return state.panel.downTime.stations;
  },
  get_causas(state){
    return state.panel.downTime.causas;
  },
  get_datainfo(state){
    return state.panel.downTime.datainfo;
  },
  get_loaderinfo(state){
    return state.loadconfig;
  },
   /* SHOWWWW */ 
  gsw_panel(state){    
    return state.panel.swpanel;
  },
  gswdwnt_panel(state){
    return state.panel.swdwnt_panel;
  },
  get_showMessage(state){
    return state.modalmessage;
  },
  get_userInfo(state){
    return state.panel.navuserinstation;
  },
  gsw_nav(state){    
    return state.panel.swnav;
  },

  get_workstation(state){
    return state.panel.navuserinstation.workstation;
  }

}


