<template>
  <div class="container text-white font-weight-bold">  
        <headerdowntime class="order-0"></headerdowntime> 
 

    <div class="row">
      <div class="col-12 col-sm-12 col-md-6 col-lg-5 col-xl-5  order-2 order-md-1 order-lg-1 ">

        <!-- downtime info -->     
        <downtimeinfo class="d-none d-sm-none d-md-block d-lg-block"></downtimeinfo>      
        <br>
        <!-- TIME Info -->
        <downtimetimeinfo></downtimetimeinfo>


      </div>



      <div class="col-12 col-sm-12 col-md-6 col-lg-7 col-xl-7 order-1 order-md-2 order-lg-1">
        <div class="row text-center">
          <div class="col">
            <img src="../../assets/img/checkin.png" class="img-fluids imgrs"  v-on:click="checkin()">
          </div>
        </div>

        <div class="row text-center">
          <div class="col">
            <b-btn class="mt-3 w-50 mb-4 font-weight-bold f1" variant="danger" @click="checkin()">CHECK IN</b-btn>
          </div>
        </div>

        <div class="row">
          <div class="col">
            <div class="row">
              <div class="col-6 col-sm-6 col-md-6 col-lg-5 col-xl-5  text-right">
                <h3 class="f3 font-weight-bold"><i class="fas fa-project-diagram"></i> LINE:</h3>
              </div>
              <div class="col-5 col-sm-6 col-md-6 col-lg-5 col-xl-5">
                <h3 class="f3 font-weight-bold"> {{get_datainfo.line_name}}</h3>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-sm-6 col-md-6 col-lg-5 col-xl-5 text-right">
                <h3 class="f3 font-weight-bold"><i class="fas fa-desktop"></i> STATION:</h3>
              </div>
              <div class="col-6 col-sm-6 col-md-6 col-lg-5 col-xl-5">
                <h3 class="f3 font-weight-bold">{{get_datainfo.station_name}}</h3>
              </div>
            </div>

            <div class="row">
              <div class="col-6 col-sm-6 col-md-6 col-lg-5 col-xl-5  text-right">
                <p class="f3 font-weight-bold"><i class="fas fa-times-circle"></i> CAUSE:</p>
              </div>
              <div class="col-6 col-sm-6 col-md-6 col-lg-5 col-xl-5">
                <p class="f3  font-weight-bold"> {{get_datainfo.cause_name}}</p>
              </div>
            </div>

            <div class="row">
              <div  class=" col-6 col-sm-6 col-md-6 col-lg-5 col-xl-5 text-right"> 
                <h3 class="f3 font-weight-bold"><i class="fas fa-chalkboard-teacher"></i> DEPARMENT:</h3>
              </div>
              <div class=" col-6 col-sm-6 col-md-6 col-lg-5 col-xl-5">
                <h3 class="f3 font-weight-bold"> {{get_datainfo.department_cause}}</h3>
              </div>
            </div>

          </div>

        </div>

      </div>

    </div>

  </div>
</template>


<script>
  import downtimeinfo from './downtimeinfo';
  import downtimetimeinfo from './downtimetimeinfo';
  import headerdowntime from './headerdowntime';



  export default {
    name: 'step3_timeCheck',
    components: {
      downtimeinfo,
      downtimetimeinfo,
      headerdowntime
    },
    data() {
      return {}
    },
    computed: {
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    },
    methods: {
      onClose: function () {
        this.$store.dispatch('close', 'panelWorkStruction');
      },
      checkin: function () {
        this.$store.dispatch('onPauseDowntime');

      }
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
  @import "../../assets/css/global.css";
  @import "../../assets/css/media_query.css";
</style>


