<template>
  <div class="container text-white">
    <headerdowntime class="order-0"></headerdowntime>

    <div class="row">
      <div class="col-12 col-sm-12 col-md-12 col-lg-5 col-xl-5  order-2 order-md-2 order-lg-1 ">
        <!-- downtime info -->
        <downtimeinfo class="d-none d-sm-none d-md-block d-lg-block"></downtimeinfo>
        <br>
        <!-- TIME Info -->
        <downtimetimeinfo></downtimetimeinfo>
      </div>


      <div class="col-12 col-sm-12 col-md-12 col-lg-7 col-xl-7 order-1 order-md-1 order-lg-1">
        <div class="row">
          <b-col cols="12" class="text-white text-center">
            <hr class="linecolor">
            <h4 class="font-weight-bold ">CANCEL</h4>
            <hr class="linecolor">
          </b-col>
        </div>

        <div class="row">
          <b-col cols="12">
            <h5 class="text-white text-left"><b>COMMENT:</b> </h5>
            <textarea class="form-control w-100" id="inputCommentCL" ref="inputCommentCL" rows="3"></textarea>
          </b-col>
        </div>

        <div class="row">
          <b-col cols="12">
            <b-btn class="mt-3 w-100 mb-5 font-weight-bold f3" variant="danger" @click="finish()">FINISH</b-btn>
          </b-col>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import downtimeinfo from './downtimeinfo';
  import downtimetimeinfo from './downtimetimeinfo';
  import headerdowntime from './headerdowntime';

  export default {
    name: 'step5_cancel',
    components: {
      downtimeinfo,
      downtimetimeinfo,
      headerdowntime
    },
    data() {
      return {}
    },
    computed: {
      get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    },
    methods: {
      finish: function () {
        var comment = document.getElementById('inputCommentCL').value;
        if (comment == "") {
         
          this.$store.dispatch('set_showMessage', 'Fill the comments');
        } else {
          var data = {
            comment: comment,
            countermeasure: "",
            permanent: 'NULL'
          };
          this.$store.dispatch('oncommentsCountermeasure', data);
        }

      },
      onClose: function () {
        this.$store.dispatch('set_swPanel', 'panelWorkStruction');
      }
    }
  }
</script>

<style>
  @import "../../assets/css/downtime.css";
  @import "../../assets/css/global.css";
  @import "../../assets/css/media_query.css";
</style>


