<template>
  <div class="mt-5">
    <div class="container">
      <div class="row mt-5 ">      

          <div class="col-12 mt-5">
          <h4> <i class="fas fa-laptop"></i> <b>{{get_workstation}}</b> </h4>
        </div>

        <div class="col mt-1 text-right">
          <img :src="get_userInfo.photo" class="user-pic" />
          <img src="../../assets/img/certificate.png" v-if="get_userInfo.iscertificated" />     
        </div>

        <div class="col-12">
          <h6> <i class="fas fa-user"></i> <br>
          <b>{{get_userInfo.name }}</b></h6>
        </div>
      </div>

      <br>

      <div class="row mt-2 uInfo">
        <div class="col-12">
          <h6> <i class="fas fa-project-diagram mr-3"></i>    <b>{{get_userInfo.line_name }}</b> </h6>
        </div>

        <div class="col-12">
          <h6> <i class="fas fa-desktop mr-3"></i> <b> {{get_userInfo.station }}</b></h6>
        </div>

        <div class="col-12">
          <h6> <i class="fas fa-mobile-alt mr-4"></i> <b> {{ get_userInfo.model }}</b></h6>
        </div>
      </div>

    </div>
  </div>

</template>

<script>
  export default {
    name: 'navuserinstation',
    data() {
      return {}
    },
    computed: {
      get_userInfo() {
        return this.$store.getters.get_userInfo;
      },
       get_workstation() {
        return this.$store.getters.get_workstation;
      },
    }
  }
</script>

<style>
  @import "../../assets/css/navuserinstation.css";
</style>


