<template>
  <nav class="navbar navbar-expand-sm  navbar-dark blackbg fixed-top">
    <a class="navbar-brand f1" href="#"  v-if="gsw_nav !== 'navuserinstation'" >Webstation Kiosk</a>.
    <a class="navbar-brand f1" href="#"   v-if="gsw_nav === 'navuserinstation'" >E-station</a>
    
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExample06" aria-controls="navbarsExample06" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse text-white" id="navbarsExample06">
      <button type="button" class="btn btn-primary ml10" v-on:click="onFullScreen()"><b>FULL SCREEN</b></button>
     <button type="button" class="btn btn-danger  ml-2"  v-on:click="onDowntime()" v-if="gsw_nav !== 'navuserinstation'"><b>DOWNTIME</b></button>     

    </div>
    <div class="text-white">
       <h6 class="f3 inline"><i class=" fas fa-desktop ml-2"></i><b> STATION: </b>
       <b class="ml-2"> {{  get_datainfo.station_name}}  </b></h6>
       <h6 class="inline ml-5 mr-5"><i class="fas fa-mobile-alt ml-2 "></i><b>MODEL:      </b>            
       <b class="ml-2"> {{ get_datainfo.model_description}} </b> </h6>
    </div>

  </nav>

</template>

<script>
  import navside from './navside';

  export default {
    name: 'navbar',
    components: {
      navside
    },
    data() {
      return {}
    },
    computed: {
      get_btnNav() {
        return this.$store.getters.btnNav;
      }, 
      gsw_panel() {
        return this.$store.getters.gsw_panel;
      },
      gsw_nav(){
        return this.$store.getters.gsw_nav;
      },
        get_datainfo() {
        return this.$store.getters.get_datainfo;
      }
    
    },
    methods: {
      onFullScreen: function () {
        this.$store.dispatch('set_swPanelonly', 'FSpanelWorkStruction');

      },
      onDowntime: function () {
        this.$store.dispatch('set_swdwntPnl', '');
      },
      onBack: function () {
        esto.$store.dispatch('set_swPanel', 'FSpanelWorkStruction');
      }
    }
  }
</script>

<style>
  @import "../../assets/css/global.css";

  .ml10{
    margin-left: 100px;
  }
  .inline{
    display: inline;
  }
</style>


